body {
    background-color: #1D2E51;
    padding: 0px;
    margin: 0;
  }

  h1{
    color:#fff;

  }

  h1, h2, p, ul, li {
    font-family: sans-serif;
    padding-left:20px;
  }

  ul.navigation-bar li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }

  ul.navigation-bar {
    background-color: #121;
    padding: 0;
    margin: 0;
  }

  ul.navigation-bar li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }

  .content {
    background-color: #FFF;
    padding-left: 60px;
    padding-top: 20px;
    padding-bottom: 20px;;
  }

  .content h2 {
    padding: 0;
    margin: 0;
  }

  .content li {
    margin-bottom: 10px;
  }

  .content form {
    padding-bottom: 20px;


  }

  .footer{
    background-color: rgba(251,251,250,0.06);
    font-family: 'Montserrat',sans-serif;
    position:relative;
    min-height:140px;
    margin-bottom:0px;
    padding-bottom: 0px;
    bottom:0;
    width:100%;
  
  }
  
  .footerSocial{
      float:left!important;
    position:relative;
    margin-right: 0px;
    margin-left: 0px;
    margin-top: 23px;
    z-index:1;
    text-align: center;
  }
  
  .footerSocial>li{
      float:right;
    list-style:none;
    padding-left:20px;
    margin-right:0px;
  }
  
  .footerSocial>li>a>img{
      width:40%;
    height:50%;
  }
  
  .footerRight{
    padding-right:45px;
    padding-left:45px;
    margin-right:auto;
    margin-left:auto;
    box-sizing:border-box;
    float:right;
  }
  
  .footerRight>li{
    text-decoration:none;
    list-style:none;
    float:left;
    padding-left:15px;
    padding-top:65px;
  
  }
  
  .footerRight>li>a{
    color:#fbfbfa;
    text-decoration:none;
    font-size:1em;
    margin:0;
  }
  
  .footerRight>li>a:hover {
      color: #9CD9F9;
  
  
    }
  
    @media screen and (max-width:600px){
  
      .footer{
        position:none;
        width:100%;
      }
  
      .footerRight{
  
        padding-right: 0px;
        padding-left: 0px;
        
          }
    
    
    
      .footerSocial>li{
         padding-left:20px;
      }
    
      .footerSocial>li>a>img{
            width:30%;
          height:30%;
        }
    }
  
  @media screen and (max-width:720px){
  
    .footer{
      
      width:100%;
    }
  
    .footerRight{
  
  padding-right: 0px;
  padding-left: 0px;
  
    }
  
  
  
    .footerSocial>li{
       padding-left:20px;
    }
  
    .footerSocial>li>a>img{
          width:30%;
        height:30%;
      }
  }
  
  @media screen and (max-width:600px){
  
    .footer{
      
      width:100%;
      font-size: 1em;
    }
  
    .footerRight{
  
      padding-right: 0px;
      padding-left: 0px;
      
        }
  
  
  
    .footerSocial>li{
       padding-left:20px;
    }
  
    .footerSocial>li>a>img{
          width:30%;
        height:30%;
      }
  }
  